.FruitItem {
    display: flex;
    padding: 20px;
    background-color: #dff5df;
    border-radius: 0 0 10px 10px;
}

.FruitItem__image {
    width: 40%;
    box-shadow: 0 5px 5px -5px #010101;
    border: 1px solid black;
    overflow: hidden;
    border-radius: 10%;
    margin-right: 20px;
    background-color: white;
}

.FruitItem__image img {
    width: 100%;
    transition: 1s;
}

.FruitItem__image img:hover {
    transform: scale(1.2);
}

.FruitItem__text {
    width: 60%;
    text-align: left;
    /*border: 1px solid black;*/
    /*overflow: hidden;*/
    /*border-radius: 10%;*/
    /*box-shadow: 0 5px 5px -5px #010101;*/

}

.FruitItem__text h1 {
    border-bottom: 1px solid gray;
}
