.FruitButton {
    background-image: linear-gradient(45deg, #fbb03b, #d4145a);
    background-position: left;
    font-weight: bold;
    font-size: 28px;
    width: 200px;
    height: 50px;
    border-radius: 5px;
    transition-property: transform, box-shadow, color;
    transition-duration: .3s, 1s, .5s;
    color: white;
    border: 1px solid white;
}

.FruitButton:hover {
    transform: scale(.95);
    box-shadow: 0 5px 5px -5px #010101;
}

.FruitButton:focus {
    outline: none;
    color: #bbb6b6;
    transform: scale(.95);
    box-shadow: 0 5px 5px -5px #010101;
}
